@import './vars'

body 
    font-family: 'sofia-pro', sans-serif
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale

a
    text-decoration: none

.wrapper
    margin: 0 auto
    width: 1200px
    display: flex

header .wrapper
    padding: 8px 60px 8px 60px
    margin: 0
    width: calc(100% - 120px)
    justify-content: space-between

header.soft
    background-color: $head   
    color: $text-white
    .links a
        font-weight: 400
        font-size: 13px
        line-height: 24px
        color: $text-white
        margin-right: 15px
    .personalisation
        display: flex
        .socials a
            color: $text-white
            margin-right: 24px
        .switcher a
            position: relative
            color: $text-white
            margin-right: 22px
            &:first-child
                margin-right: 52px
            &::after
                position: absolute
                right: -22px
                top: calc(50%  -  4px)
                content: ''
                width: 12px
                height: 12px
                background-image: url(./img/icons/arrow.svg)

.best-selling
    &__wrapper
        flex-direction: column
    &__title
        text-align: center
    &__cloth-options a    
        margin-right: 40px 
        color: $second
        position: relative
        transition-duration: 0.4s
        &.active
            color: $head
            &::before
                transition-duration: 0.4s
                content: ''
                bottom: -7px
                position: absolute
                width: 90%
                height: 2px
                background-color: $head
        &:hover
            color: $head
            .best-selling__cloth-options a.active
                color: $second
            &::before
                width: 90%
        &::before
            transition-duration: 0.4s
            content: ''
            bottom: -7px
            position: absolute
            width: 0
            height: 2px
            background-color: $head
        &:last-child
            margin: 0
    &__container
        display: flex
        justify-content: space-between
        margin-top: 50px 
    &__item
        width: 280px
    &__image
        width: 280px
        height: 340px
        position: relative
        overflow: hidden
        &:hover .best-selling__cart
            bottom: 0
        img
            height: 340px
            width: 280px
    &__tip
        color: $third
        font-weight: 400
        font-size: 14px
        line-height: 24px
        margin-bottom: 2px 
    &__name
        margin-top: 0
        margin-bottom: 0 
        font-weight: 400
        font-size: 16px
        line-height: 18px
    &__cost
        margin-top: 8px
        color: $second
    &__cart
        padding: 16px 0
        background-color: $head
        color: $text-white
        display: flex
        justify-content: space-around
        position: absolute
        width: 100%
        bottom: -50.4px
        left: 0
        transition-duration: 0.4s
    &__like
        position: absolute
        color: $head
        background-color: $text-white
        top: 20px
        right: 20px
        border-radius: 20px
        padding: 13px 12px 11px 12px
        &:active
            background-color: $red
        &:hover
            color: $red
            &:active
                color: $text-white
            


.shop-category
    &__wrapper
        flex-direction: column
    &__title
        text-align: center
        margin-top: 100px
    &__container
        display: flex
        justify-content: space-between
        margin-top: 30px 
    &__item img
        width: 230px
    &__tip
        text-align: center
        color: $head
        font-weight: 500
        font-size: 16px
        line-height: 22px
        margin-bottom: 2px
    &__products
        text-align: center
        margin-top: 5px
        color: $second
        font-weight: 400
        font-size: 14px
        line-height: 24px

.new-collection
    margin-top: 72px
    background-image: url("./img/productBack.png")
    &__container
        text-align: center
        padding: 200px 0
        width: 100%
        h1
            font-weight: 700
            font-size: 60px
            line-height: 84px
            color: $text-white
            margin-bottom: 0
        p
            font-weight: 400
            font-size: 16px
            line-height: 21px
            color: $text-white
            margin-bottom: 46px
        a
            width: 210px
            padding: 18px 68px 13px 68px
            background-color: $text-white
            color: $head
            font-weight: 500
            font-size: 14px
            line-height: 24px

.latest-prdoucts
    &__wrapper
        flex-direction: column
    &__title
        margin-top: 92px
        text-align: center
    &__container
        display: flex
        justify-content: space-between
        margin-top: 32px 
    &__image img
        width: 280px
    &__tip
        margin-top: 12px
        margin-bottom: 2px 
        color: $third
        font-weight: 400
        font-size: 14px
        line-height: 24px
    &__name
        margin-top: 0
        margin-bottom: 0 
        font-weight: 400
        font-size: 16px
        line-height: 21px
    &__cost
        margin-top: 6px
        font-weight: 400
        font-size: 16px
        line-height: 21px
        color: $second
    &____cost-title
        display: flex
    &__last-cost
        color: $second
        text-decoration-line: line-through
    &__new-cost
        color: $head

.latest-news
    &__wrapper
        flex-direction: column
    &__title
        margin-top: 52px
        text-align: center
    &__container
        display: flex
        justify-content: space-between
        margin-top: 34px
    &__item img
        width: 390px
        height: 240px
        margin-bottom: 0
    &__info
        margin-top: 5px
        margin-bottom: 4px
        font-weight: 400
        font-size: 14px
        line-height: 24px 
        color: $second
    &__short
        margin-top: 0
        font-weight: 400
        font-size: 18px
        line-height: 24px
        color: $head

